import React from 'react';

import './style.scss';

const data = [
    {
        image: '1',
        brand: 'Abat',
        date: '22.06.17',
        title: '        Abat',
        text: '             1  2017            Abat  ...  38-01          .'
    },
    {
        image: '2',
        brand: 'Abat',
        date: '22.06.17',
        title: '        Abat',
        text: '             1  2017     ...'
    },
    {
        image: '1',
        brand: 'Abat',
        date: '22.06.17',
        title: '        Abat',
        text: '             1  2017            Abat  ...  38-01          .'
    }
];

const elements = data.map((item, id) => {
    return (
        <div className='News__Item' key={id}>
            <div className='News__ItemImage'>
                <img src={require(`../../assets/image/News/${item.image}.png`)} alt={' ' + id} />
            </div>
            <div className='News__ItemContent'>
                <div className='News__ItemHeader'>
                    <span>{item.brand}</span>
                    <span>{item.date}</span>
                </div>
                <span>{item.title}</span>
                <span>{item.text}</span>
            </div>
        </div>
    );
});

const News = () => {
    return (
        <div className='News'>
            <div className='News__Header'>
                <span> </span>
            </div>

            <div className='News__Content'>
                <div className='News__Main'>
                    <img src={require('../../assets/image/News/main.png')} alt='main news'/>
                    <div className='News__Text'>
                        <div>
                            <span>Abat</span>
                            <span>22.06.17</span>
                        </div>
                        <span>     -18-8-6</span>
                        <span>
                                 -18-8-6:    
                            - -18  18  600800   - - 
                            135   7      
                              .   110  ( 4-    ) 
                            USB-.   10     0  100%. 5 
                        </span>
                    </div>
                </div>

                <div className='News__Panel'>
                    { elements }
                </div>
            </div>

            <div className='News__Footer'>
                <button className='News__Button' type='button'> </button>
            </div>
        </div>
    );
};

export default News;