import React from 'react';

import './style.scss';

const data = [
    {
        description: '',
        image: '1'
    },
    {
        description: '',
        image: '2'
    },
    {
        description: '',
        image: '3'
    },
    {
        description: '',
        image: '4'
    },
    {
        description: '',
        image: '5'
    },
    {
        description: ' ',
        image: '6'
    },
    {
        description: '',
        image: '7'
    },
    {
        description: '  ',
        image: '8'
    },
    {
        description: ' ',
        image: '9'
    },
    {
        description: '',
        image: '10'
    },
    {
        description: ' ',
        image: '11'
    },
];

const CatalogMenu = () => {

    const elements = data.map((item, id) => {
        return (
            <div className='CatalogMenu__Item' key={id}>
                <div className='CatalogMenu__Circle'>
                    <img src={require(`../../assets/image/CatalogMenu/${item.image}.png`)} alt='1'/>
                </div>
                <span>{item.description}</span>
            </div>
        );
    })

    return (
        <div className='CatalogMenu'>
            {elements}
        </div>
    );
};

export default CatalogMenu;